/* variables */
/* mixins */
@import url("https://fonts.googleapis.com/css?family=Quicksand:300,400,500,700");
.col-xs-5ths,
.col-sm-5ths,
.col-md-5ths,
.col-lg-5ths {
  position: relative;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
}

.col-xs-5ths {
  width: 20%;
  float: left;
}

@media (min-width: 768px) {
  .col-sm-5ths {
    width: 20%;
    float: left;
  }
}

@media (min-width: 992px) {
  .col-md-5ths {
    width: 20%;
    float: left;
  }
}

@media (min-width: 1200px) {
  .col-lg-5ths {
    width: 20%;
    float: left;
  }
}

/*------------------------- STRUCTURE --------------------------------*/
body {
  font-family: 'Lato', sans-serif;
  font-weight: 100;
}

h1, h2, h3, .menu_all li a {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
}

html {
  height: 100%;
}

body {
  height: 100%;
  color: black;
  background-color: black;
}

.animate_all, .btn, .menu_all, .loginbtn_all, .menuaddlink_all, .popupmenu_all .button, .login_all .step .form-control, .signup_all .step .form-control, .link_all, .link_all .image, .collection_all, .collection_all .image, .addlink_page .addlink_all .content .form-control.textbox, .search_page .searchbox, .search_page .searchbox .form-control {
  -webkit-transition: all .2s ease-out 0s;
  -moz-transition: all .2s ease-out 0s;
  -o-transition: all .2s ease-out 0s;
  transition: all .2s ease-out 0s;
}

h1, h2, h3, h4, h5, h6, h7 {
  margin: 0px;
  font-weight: 600;
}

h1, h2, h3 {
  margin-bottom: 20px;
}

h2 {
  font-size: 35px;
}

a, a:hover {
  color: black;
}

.btn {
  border: solid 2px white;
  font-size: 12px;
  padding: 10px 20px;
  text-transform: capitalize;
  background-color: transparent;
  text-transform: uppercase;
  font-weight: 600;
  border-radius: 0px;
}

.btn:hover {
  background-color: rgba(255, 255, 255, 0.3);
  border: solid 2px white;
  border-radius: 0px;
}

@media (max-width: 768px) {
  .btn {
    padding: 10px 20px;
  }
}

.btn-lg, .btn-lg:hover {
  padding: 20px 40px;
  font-size: 20px;
}

.row-no-gutter > [class*='col-'] {
  padding-right: 0;
  padding-left: 0;
}

::-webkit-scrollbar {
  width: 0.5em;
  height: 0.5em;
}

::-webkit-scrollbar-thumb {
  background: #0d0d0d;
}

::-webkit-scrollbar-track {
  background: white;
}

body {
  scrollbar-face-color: #0d0d0d;
  scrollbar-track-color: white;
}

.nav_all .navbar {
  margin: 0px;
  background-color: white;
}

.nav_all .navbar-nav {
  margin: 0px;
  float: none;
}

.nav_all .navbar-nav .dropdown-menu {
  transform: translateX(25%);
}

.overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0px;
  left: 0px;
}

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}

.ng-scope {
  height: 100%;
}

.section {
  background-color: white;
  width: 100%;
  min-height: 800px;
}

.section .inner {
  padding: 50px;
}

.wrap {
  margin: 0px 10px;
}

.page {
  width: 100%;
  height: 100%;
}

.loadingicon {
  background-image: url(../../images/app/ring.gif);
  background-position: center center;
  background-repeat: no-repeat;
  height: 50px;
}

/*------------------------- APP --------------------------------*/
/* browse */
.app_all {
  width: 100%;
  height: 100%;
  position: relative;
}

.gradient_all {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100px;
  z-index: 4;
  background: linear-gradient(rgba(0, 0, 0, 0.4), transparent);
}

.topbanner_all {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 320px;
  z-index: 0;
  background-image: radial-gradient(farthest-corner at -60% 10%, #00cca3 5%, #8f08fd 100%);
}

.logo_all {
  position: fixed;
  top: 20px;
  left: 20px;
  width: 200px;
  z-index: 5;
}

.menu_all {
  position: fixed;
  top: 20px;
  right: 20px;
  transform: scale(1);
  z-index: 5;
}

.menu_all li a {
  color: white;
  cursor: pointer;
}

.menu_all li a.background {
  background-color: white;
  color: black;
}

.menu_all li a.background:hover, .menu_all li a.background:active, .menu_all li a.background:focus {
  background-color: white;
}

.menu_all li a:hover, .menu_all li a:active, .menu_all li a:focus {
  background-color: transparent;
}

.menu_all .button {
  cursor: pointer;
  position: relative;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  font-size: 23px;
  background-color: white;
}

.menu_all .button i {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
}

.loginbtn_all {
  position: fixed;
  top: 20px;
  right: 90px;
  transform: scale(1);
  z-index: 5;
}

.loginbtn_all:hover {
  transform: scale(1.2);
}

.loginbtn_all .button {
  cursor: pointer;
  position: relative;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  font-size: 23px;
  background-color: #b3b3b3;
}

.loginbtn_all .button i {
  color: white;
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
}

.menuaddlink_all {
  position: fixed;
  top: 20px;
  color: white;
  right: 160px;
  transform: scale(1);
  z-index: 5;
}

.menuaddlink_all:hover {
  transform: scale(1.2);
}

.menuaddlink_all .button {
  cursor: pointer;
  position: relative;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  font-size: 23px;
  background-color: black;
}

.menuaddlink_all .button i {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
}

.popupmenu_all {
  position: fixed;
  top: 0opx;
  width: 100%;
  z-index: 100;
  background-color: black;
}

.popupmenu_all.ng-hide {
  animation: 0.8s slideOutUp ease;
}

.popupmenu_all.ng-hide-remove {
  animation: 0.8s slideInDown ease;
}

.popupmenu_all .button {
  cursor: pointer;
  text-align: center;
  padding: 60px 0px;
  font-size: 20px;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.8);
  border-left: solid 1px rgba(255, 255, 255, 0.3);
}

.popupmenu_all .button:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.login_all {
  background-color: black;
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.login_all.ng-hide {
  animation: 0.8s slideOutDown ease;
}

.login_all.ng-hide-remove {
  animation: 0.8s slideInUp ease;
}

.login_all .close {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 10;
}

.login_all .close img {
  width: 20px;
}

.login_all .step {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
}

.login_all .step.ng-hide {
  animation: 0.8s slideOutLeft ease;
}

.login_all .step.ng-hide-remove {
  animation: 0.8s slideInRight ease;
}

.login_all .step h2 {
  color: white;
  font-weight: 300;
}

.login_all .step .form-control {
  padding: 40px 30px;
}

.login_all .step .form-control:focus {
  border-color: #00cca3;
  transform: scale(1.05);
}

.login_all .step .textbox_holder {
  margin-bottom: 20px;
}

.login_all .step .center {
  position: absolute;
  width: 500px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.signup_all {
  background-color: black;
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.signup_all.ng-hide {
  animation: 0.8s slideOutDown ease;
}

.signup_all.ng-hide-remove {
  animation: 0.8s slideInUp ease;
}

.signup_all .close {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 10;
}

.signup_all .close img {
  width: 20px;
}

.signup_all .step {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
}

.signup_all .step.ng-hide {
  animation: 0.8s slideOutLeft ease;
}

.signup_all .step.ng-hide-remove {
  animation: 0.8s slideInRight ease;
}

.signup_all .step h2 {
  color: white;
  font-weight: 300;
}

.signup_all .step .form-control {
  padding: 40px 30px;
}

.signup_all .step .form-control:focus {
  border-color: #00cca3;
  transform: scale(1.05);
}

.signup_all .step .textbox_holder {
  margin-bottom: 20px;
}

.signup_all .step .center {
  position: absolute;
  width: 500px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.page_all.ng-enter {
  animation: 0.8s fadeIn ease;
}

/*------------------------- REUSE --------------------------------*/
.link_all {
  border-radius: 4px;
  background-color: white;
  overflow: hidden;
  margin-bottom: 20px;
  transform: scale(1);
}

.link_all:hover {
  transform: scale(1.02);
}

.link_all .image {
  background-color: #f7f7f7;
  cursor: pointer;
  height: 210px;
  background-size: cover;
  opacity: 1;
}

.link_all .text {
  padding: 12px 15px;
}

.link_all h2 {
  font-size: 18px;
  margin-bottom: 6px;
  color: #333333;
  height: 18px;
  overflow: hidden;
}

.link_all h3 {
  font-size: 12px;
  margin-bottom: 0px;
  color: #cccccc;
  float: left;
}

.link_all h3.views {
  float: right;
}

.loading .link_all {
  background-color: white;
}

.loading .link_all:hover {
  transform: scale(1);
}

.loading .link_all .image {
  background-color: #f7f7f7;
}

.loading .link_all h2 {
  background-color: #f0f0f0;
  width: 40%;
}

.loading .link_all h3 {
  background-color: #f7f7f7;
  width: 100px;
}

.loading .link_all h3.views {
  background-color: #f7f7f7;
  width: 100px;
}

.collection_all {
  border-radius: 4px;
  background-color: white;
  overflow: hidden;
  margin-bottom: 20px;
  transform: scale(1);
  color: black;
}

.collection_all:hover {
  transform: scale(1.02);
}

.collection_all .image {
  background-color: #f7f7f7;
  cursor: pointer;
  height: 210px;
  background-size: cover;
  opacity: 1;
}

.collection_all .text {
  padding: 12px 15px;
}

.collection_all h2 {
  font-size: 18px;
  margin-bottom: 6px;
  color: #333333;
  height: 18px;
  overflow: hidden;
}

.collection_all h3 {
  font-size: 12px;
  margin-bottom: 0px;
  color: #cccccc;
  float: left;
}

.collection_all h3.views {
  float: right;
}

